import * as React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
//import { StaticImage } from "gatsby-plugin-image"
import LayoutNoFooter from "../components/LayoutNoFooter"

export default function announcements({data}) {  
  const sponsors = data.allFile.nodes.filter((item)=>item.relativePath.includes('sponsors'))
  
  return (<>
    <LayoutNoFooter>
      <div>
        {sponsors.map((image, i) => <GatsbyImage key={i} image={getImage(image)} alt='' />)}
      </div>
    </LayoutNoFooter>
  </>)
}

export const query = graphql`
query sponsorsPage {
allFile {       
  nodes {        
    relativePath         
    childImageSharp {         
      gatsbyImageData(width: 130)
    }
   }
  }
}
`
